@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
.homepage{
    display: grid;
    grid-template-columns: 40% 60%;
    padding: 1em;
    grid-column-gap:1em;
    grid-row-gap: 1.5em;
    grid-auto-rows: minmax(300px, auto);
    grid-auto-columns: minmax(300px, auto);
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
}

.homepage > div{
    padding: 20px;
    font-size: 30px;
    text-align: center;
    border-radius: 10px;
}

.leaderboard-box{
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
}

.leaderboard-box:hover{
    background-color: lightgreen;
}

.link{
    text-decoration: none;
}

.progress {
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
    height: 285px;
}

.progress:hover{
    background-color: lightgreen;
}
.Profile{
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
}
.StartButton{
    width: 400px;
    height: 200px;
    background: #f3f0f1;
    position: relative;
    background: #f3f0f1;
    border-radius: 32px;
    text-align: center;
    cursor: pointer;
    transition: all 0.1s ease-in-out;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2em;
}

.StartButton-text {
    line-height: 100px;
    font-family: "Montserrat", sans-serif;
    font-size: 32px;
    font-weight: semibold;
  }

.StartButton {
    box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8), 6px 6px 10px rgba(0, 0, 0, 0.2);
    color: #6f6cde;
  }
.StartButton:hover {
    opacity: 0.5;
    background-color: lightgreen;
    box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8), 6px 6px 10px rgba(0, 0, 0, 0.2);
  }
.StartButton:active {
    opacity: 1;
    box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5), inset 8px 8px 16px rgba(0, 0, 0, 0.1);
    color: #79e3b6;
  }
.video-title-container {
    grid-area: title;
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
    height: 6em;
    text-align: center;
}

.video-next {
    grid-area: next;
    width: 10em;
    margin-left: auto;
    margin-right: auto;
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
    height: 4em;
    text-align: center;
    margin-top: 15em;
}

.video-home {
    grid-area: home;
    width: 10em;
    margin-left: auto;
    margin-right: auto;
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
    height: 4em;
    text-align: center;
    margin-top: 15em;
}

.video-container{
    grid-area: video;
    margin-left: auto;
    margin-right: auto;
    height: 7em;
}

.video{
    width: 700px;
}

.videoPage{
    display: grid;
    grid-template-areas:
      'title title'
      'video video'
      'home next';
    grid-gap: 3em;
    margin-left: auto;
    margin-right: auto;
    max-width: 1000px;
}

.video-text{
    font-family: Garamond, serif;
    font-size: 1.5em;
}

.video-title{
    font-size: 3em;

}
.LeaderBoard {
    display: grid;
    grid-template-columns: auto;
    margin-left: auto;
    margin-right: auto;
    max-width: 1000px;
    grid-gap: 3em;
}


.leaderboard-title-container{
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
    height: 6em;
    text-align: center;
}

.leaderboard-home-container{
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
    height: 6em;
    text-align: center;
    width: 400px;
    margin-left: auto;
    margin-right: auto;
}


.leaderboard-container {
    width: 600px;
    height: auto;
    margin-left: auto;
    margin-right: auto;
  }
  
  .row {
    position: relative;
    display: block;
    width: 100%;
    height: 60px;
    border-bottom: 1px solid #AFAFAF;
  }
  
  .name {
    position: relative;
    display: inline-block;
    width: 75%;
    line-height: 45px;
  }
  
  .score {
    position: relative;
    display: inline-block;
    width: 25%;
  }
  
  .row:nth-child(1) {
    background: gold;
  }
  
  .row:nth-child(2) {
    background: #c0c0c0;
  }
  
  .row:nth-child(3) {
    background: #cd7f32;
  }

.leaderboard-title {
    font-family: Garamond, serif;
    font-size: 3em;
}

.leaderboard-home{
    font-family: Garamond, serif;
    font-size: 2em;
}

.name-text{
    font-family: Garamond, serif;
    font-size: 1.5em;
}
.transition-message {
    grid-area: message;
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
    height: 6em;
    text-align: center;
    margin-top: 2em;
}

.transition-next {
    grid-area: next;
    width: 10em;
    margin-left: auto;
    margin-right: auto;
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
    height: 7em;
    text-align: center;
}

.transition-home {
    grid-area: home;
    width: 10em;
    margin-left: auto;
    margin-right: auto;
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
    height: 7em;
    text-align: center;
}


.Transition{
    display: grid;
    grid-template-areas:
      'message message'
      'home next';
    grid-gap: 3em;
    margin-left: auto;
    margin-right: auto;
    max-width: 1000px;
    grid-row-gap: 10em;
    row-gap: 10em;
}

.transition-text{
    font-family: Garamond, serif;
    font-size: 1.5em;
}

.transition-title{
    font-size: 3em;

}
